import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state={//state
     selectMenu: "",
     city:"",
     department:"",
     station:"",
     olt:"",
     timelist:[],
     //
   };
const getters = {   //state()
    getSelectMenu(state) {  //showFooter
       return state.selectMenu
    },
    getCity(state) {  //showFooter
        return state.city
     },
     getDepartment(state) {  //showFooter
        return state.department
     },
     getStation(state) {  //showFooter
        return state.station
     },
     getOLT(state) {  //showFooter
        return state.olt
     },
     getTimeList(state) {  //showFooter
        return state.timelist
     },
    //  getStationList(state) {  //showFooter
    //     return state.stationlist
    //  },
    //  getOLTList(state) {  //showFooter
    //     return state.oltlist
    //  },
};
const mutations = {
    // show(state) {   //statestate();
    //     state.showFooter = true;
    // },
    // hide(state) {  //
    //     state.showFooter = false;
    // },
    setSelectMenu(state,menu){ //statesum
       state.selectMenu=menu
    },
    setCity(state,_city){ //statesum
        state.city=_city
     },
     setDepartment(state,_department){ //statesum
        state.department=_department
     },
     setStation(state,_station){ //statesum
        state.station=_station
     },
     setOLT(state,_OLT){ //statesum
        state.olt=_OLT
     },
     setTimeList(state,_timelist){ //statesum
        state.timelist=_timelist
     },
    //  setStationList(state,_stationlist){ //statesum
    //     state.stationlist=_stationlist
    //  },
    //  setOLTList(state,_OLTlist){ //statesum
    //     state.oltlist=_OLTlist
    //  },
};
 const actions = {
    // hideFooter(context) {  //mutationscontextstore 
    //     context.commit('hide');
    // },
    // showFooter(context) {  //
    //     context.commit('show');
    // },
    getNewMenu(context,menu){   //num
        context.commit('setSelectMenu',menu)
     },
     getNewCity(context,_city){   //num
        context.commit('setCity',_city)
     },
     getNewDepartment(context,_department){   //num
        context.commit('setDepartment',_department)
     },
     getNewStation(context,_station){   //num
        context.commit('setStation',_station)
     },
     getNewOLT(context,_OLT){   //num
        context.commit('setOLT',_OLT)
     },
     getNewTimeList(context,_timelist){   //num
        context.commit('setTimeList',_timelist)
     },
    //  getNewStationList(context,_stationlist){   //num
    //     context.commit('setStationList',_stationlist)
    //  },
    //  getNewOLTList(context,_OLTlist){   //num
    //     context.commit('setOLTList',_OLTlist)
    //  },
};
const store = new Vuex.Store({
       state,
       getters,
       mutations,
       actions
});
export default store;


